// =======================================
// =            Global Config            =
// =======================================

// Tipograf칤a
$font-url--google       : "http://fonts.googleapis.com/css?family=Raleway:100,300,400,700";
$font-family--primary   : "Raleway", "Helvetica Neue", Helvetica, Arial, sans-serif;

// Texto
$base__font-size        : 16px;
$base__line             : 1.65;
$letter-space           : 1px;

// Modular Scale - https://github.com/modularscale/modularscale-sass/tree/3.x
$ms-base:  em($base__font-size);
$ms-ratio: $fourth;


// Pesos Tipogr치ficos
$fw--thin               : 100;
$fw__extra-light        : 200;
$fw--light              : 300;
$fw--regular            : 400;
$fw--medium             : 500;
$fw--semi-bold          : 600;
$fw--bold               : 700;
$fw--extra-bold         : 800;
$fw--ultra-bold         : 900;


// Colores Descriptivos
$white                  : #ffffff;
$black                  : #4a4a4a;
$gris                   : #797e83;
$orange                 : #EF6E30;
$blue                   : #a0dae0;
$dark-blue              : #3e466e;
$yellow                 : #fdc75a;
$purple                 : #622567;

$main-color             : $dark-blue;


// Uso de Colores
$color-acento           : invert($main-color);
$color-sombra           : rgba($black, 0.2);

// Transiciones
$trns-duration          : .3s;

// Breakpoints para los media queries 游냤
@import "breakpoints";

$column-gutter          : 20px;
